<template>
    <div style="background-color:#fff;">
        <p class="flex-vertical" style="height:60px;padding:0 16px;margin-bottom:16px;font-size:18px;">修改支付密码</p>
        <van-form @submit="_submit_edit">
            <van-field label="账号" v-model="userInfoForm.viewCode" disabled />
            <van-field label="手机号" v-model="userInfoForm.userMobile" disabled />
            <van-field label="姓名" v-model="userInfoForm.userName" disabled />
            <van-field
                label="新支付密码"
                placeholder="新支付密码"
                v-model="userInfoForm.userNameEn"
                :rules="[{ required: true, message: '请填写新的支付密码' }]"
            />
            <van-field
                v-model="userInfoForm.userIdcard"
                center
                clearable
                label="短信验证码"
                placeholder="短信验证码"
                :rules="[{ required: true, message: '请填写短信验证码' }]"
            >
                <template #button>
                    <van-button
                        size="small"
                        :disabled="!canSendSms"
                        @click="_send_sms"
                        native-type="button"
                    >{{sendSmsBtnText}}</van-button>
                </template>
            </van-field>
            <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit">提交</van-button>
            </div>
        </van-form>
    </div>
</template>

<script>
import { getPersonalInfo, editPersonalInfo, sendSms } from '@/api';
export default {
    data:()=>({
        sendingSms: false,
        waitSmsTime: 60,
        countTimer: null,
        phoneNumberPattern: /^1[3456789]\d{9}$/,
        sendSmsBtnText: '发送验证码',
        userInfoForm: {
            userName: '',
            userMobile: '',
            userNameEn: '',
            userIdcard: '',

            viewCode: ''
        }
    }),
    computed: {
        canSendSms: function(){
            if(
                !this.sendingSms 
                && this.phoneNumberPattern.test(this.userInfoForm.userMobile)
            ) return true;
            return false;
        }
    },
    mounted(){
        this.getPersonalInfo();
    },
    destroyed(){
        clearInterval(this.countTimer);
    },
    methods: {
        getPersonalInfo(){
            getPersonalInfo().then(res=>{
                if(res.code === 0){
                    this.userInfoForm.viewCode = res.data.viewCode;
                    this.userInfoForm.userMobile = res.data.userMobile;
                    this.userInfoForm.userName = res.data.userName;

                }else{
                    this.$toast.fail(res.msg);
                }
            })
        },
        _send_sms(){
            let { userName, userMobile } = this.userInfoForm;
            sendSms({
                phoneNumber: userMobile,
                username: userName
            }).then(res=>{
                if(res.code === 0){
                    this.$toast.success('发送验证码成功！');
                    this.countTimer = setInterval(()=>{
                        if(this.waitSmsTime === 0){
                            clearInterval(this.countTimer);
                            this.sendingSms = false;
                            this.sendSmsBtnText = '发送验证码';
                            this.countTimer = 60;
                        }else{
                            this.sendingSms = true; 
                            this.sendSmsBtnText = --this.waitSmsTime + '秒后可再发送';
                        }
                    },1000)
                }else{
                    this.$toast.fail(res.msg);
                }
            })
        },
        _submit_edit(){

            let { userName, userNameEn, userIdcard, userMobile } = this.userInfoForm;

            editPersonalInfo({
                userName,
                userNameEn,
                userIdcard,
                userMobile
            }).then(res=>{
                if(res.code === 0){
                    this.$toast.success('修改支付密码成功！');
                    this.getPersonalInfo();
                }else{
                    this.$toast.fail(res.msg);
                }
            })
        }
    }
}
</script>

